
import { defineComponent, ref } from "vue";
export default defineComponent({
  props: {
    status: String,
  },
  components: {},
  emits: ["friend-add"],
  setup(props, { emit }) {
    const friendName = ref("");
    const friendAge = ref(21);
    const addFriend = () => {
      emit("friend-add", {
        name: friendName.value,
        age: friendAge.value,
      });
    };

    const resetForm = () => {
      friendName.value = "";
      friendAge.value = 21;
    };
    return {friendName, friendAge, addFriend, resetForm};
  },
});
